@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #e0e9f5;
}

main {
  margin-top: 7rem;
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}
button{
  color: #e0e9f5;
  padding: 10px;
  width: 90px;
  background-color: #042b5f;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 2px;
}

button:hover{
  background-color: #4a6588;
}

ul{
  list-style: none;
}


.Header_header__2Gy_J {
  width: 100%;
  height: 5rem;
  background-color: #044599;
  padding: 0 10%;
}

.Header_header__2Gy_J nav {
  height: 100%;
}

.Header_header__2Gy_J ul {
  height: 100%;
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  align-items: center;
  justify-content: center;
}

.Header_header__2Gy_J li {
  margin: 0 1rem;
  width: 5rem;
}

.Header_header__2Gy_J a {
  color: white;
  text-decoration: none;
}

