@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #e0e9f5;
}

main {
  margin-top: 7rem;
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}
button{
  color: #e0e9f5;
  padding: 10px;
  width: 90px;
  background-color: #042b5f;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 2px;
}

button:hover{
  background-color: #4a6588;
}

ul{
  list-style: none;
}

